import React from 'react';
import s from "./Post.module.css";
const Post = (props) => {
  return (
    
      <div className={s.item}>
        <img className="ava" src="data:image/jpeg;base64,"/>
        {props.message}
        <div>
        <span>{props.likesCount}</span>
        </div>
        </div>
      
  )
}
export default Post;